import {routerMode} from '../utils/base'
import Vue from 'vue'
import Router from 'vue-router'

const index = r => require.ensure([], () => r(require('../pages/index/index')), 'index')
const home = r => require.ensure([], () => r(require('../pages/home/index')), 'home')
const disclose = r => require.ensure([], () => r(require('../pages/disclose/index')), 'disclose')
const miss = r => require.ensure([], () => r(require('../pages/404/index')), 'miss')

Vue.use(Router)

export default new Router({
  mode: routerMode,
  routes: [
    {
      path: '/',
      component: index,
      children: [
        { path: '', component: home },
        { path: '/disclose', component: disclose }
      ]
    },
    { path: '*', component: miss }
  ]
})



